.home {
  padding: 50px;
}

.hero-section {
  text-align: center;
}

.title {
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 20px;
}

.subtitle {
  font-size: 24px;
  margin-bottom: 50px;
}

.button {
  display: inline-block;
  background-color: #e50914;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  padding: 15px 30px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #d30c12;
}

.categories {
  margin-top: 50px;
}

.category-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.movie-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

/* Example movie card styles */
.movie-card {
  width: calc(25% - 10px);
  margin-bottom: 20px;
}

.movie-card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.movie-card .title {
  font-size: 18px;
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 5px;
}

.movie-card .year {
  font-size: 14px;
  color: #666;
  margin-bottom: 10px;
}
